.Wrapper {
  max-width: 1100px;
  padding: 0 20px;
  margin: 0 auto;
  /* background-color: #B3D7F2; */
  background-color: white;
  height: 100vh;
}

.NextBtn {
  position: absolute;
  bottom: 0;
  margin: 10px;
  height: 27px;
  width: 50px;
  /* font-weight: bold; */
  border-radius: 3px;
  border: 1px solid black;
}

.NextBtn:hover {
  cursor: pointer;
  background-color: lightgrey;
}